/* eslint-disable import/no-cycle */
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { USERINFO } from '../constants/localStorage';

import { AppThunk } from '../reducers/rootReducer';
import { Paths } from '../router/routes';
import { login, UserInfo } from '../services/authService';
import { navigate } from '../services/historyService';

type AuthSliceState = {
	userInfo?: UserInfo;
};

const initialState: AuthSliceState = {
	userInfo: undefined,
};

const authSlice = createSlice({
	name: 'auth',
	initialState,
	reducers: {
		setUserInfo(state, action: PayloadAction<UserInfo>) {
			const { payload: userInfo } = action;
			return {
				...state,
				userInfo,
			};
		},
	},
});

export const { setUserInfo } = authSlice.actions;

export const wordpressLogin =
	(username: string, password: string): AppThunk =>
	async dispatch => {
		try {
			const userInfo = await login(username, password);
			localStorage.setItem(USERINFO, JSON.stringify(userInfo));
			dispatch(setUserInfo(userInfo));
			navigate(Paths.Lesson);
		} catch (err) {
			// dispatch(getIssuesFailure(err.toString()));
		}
	};

export default authSlice.reducer;
