import React, { useEffect, useState } from 'react';
import CourseCard from '../../components/common/CourseCard/CourseCard';
import { Paths } from '../../router/routes';
import { CourseInfo, getCourses } from '../../services/courseService';
import { navigate } from '../../services/historyService';
import { getUsers } from '../../services/userService';

function Courses(): React.ReactElement {
	const [courses, setCourses] = useState<CourseInfo[]>([]);
	const [users, setUsers] = useState<any[]>([]);

	useEffect(() => {
		getCourses()
			.then(c => {
				setCourses(c);
				return c;
			})
			.catch(() => {});

		getUsers()
			.then(u => {
				setUsers(u);
				return u;
			})
			.catch(() => {});
	}, []);

	return (
		<div style={{ padding: '64px' }}>
			{courses.map(c => {
				const tutor = users.find(u => u.id === c.author);
				return (
					<CourseCard
						onClick={() => {
							navigate(Paths.Lesson.replace(':courseId', c?.id?.toString()));
						}}
						title={c.title.rendered}
						author={tutor?.name ?? 'CourseLagoon'}
					/>
				);
			})}
		</div>
	);
}

export default Courses;
