import axios from 'axios';
import { COURSE_ENDPOINT, MAIN_ENDPOINT } from './urlService';

export interface CourseInfo {
	id: number;
	date: string;
	date_gmt: string;
	guid: { rendered: string };
	modified: string;
	modified_gmt: string;
	slug: string;
	status: string;
	type: string;
	link: string;
	title: {
		rendered: string;
	};
	content: {
		rendered: string;
		protected: boolean;
	};
	author: number;
	featured_media: number;
	menu_order: number;
	template: string;
	categories: [];
	tags: [];
	ld_course_category: [];
	ld_course_tag: [];
	materials_enabled: boolean;
	materials: { rendered: string };
	certificate: number;
	disable_content_table: boolean;
	lessons_per_page: boolean;
	lesson_per_page_custom: number;
	topic_per_page_custom: number;
	price_type: string;
	price_type_paynow_price: string;
	price_type_subscribe_price: string;
	price_type_closed_price: string;
	price_type_closed_custom_button_url: string;
	prerequisite_enabled: boolean;
	prerequisite_compare: string;
	prerequisites: string[];
	points_enabled: boolean;
	points_access: string;
	points_amount: string;
	progression_disabled: boolean;
	expire_access: boolean;
	expire_access_days: number;
	expire_access_delete_progress: boolean;
	_links: {
		self: { href: string }[];
		collection: { href: string }[];
		about: { href: string }[];
		author: { embeddable: boolean; href: string }[];
		'version-history': { count: number; href: string }[];
		'predecessor-version': { id: number; href: string }[];
		steps: { embeddable: boolean; href: string }[];
		users: { embeddable: boolean; href: string }[];
		groups: { embeddable: boolean; href: string }[];
		'price-types': { embeddable: boolean; href: string }[];
		prerequisites: { embeddable: boolean; href: string }[];
		'wp:featuredmedia': { embeddable: boolean; href: string }[];
		'wp:attachment': { href: string }[];
		'wp:term': {
			taxonomy: string;
			embeddable?: boolean;
			href?: string;
		}[];
		curies: { name: string; templated: boolean }[];
	};
}

export async function getCourseById(courseId: string | number): Promise<CourseInfo[]> {
	const url = `${MAIN_ENDPOINT}/${COURSE_ENDPOINT.GET_COURSES}?include=${courseId}`;
	const { data } = await axios.get<CourseInfo[]>(url);

	return data;
}

export async function getCourses(): Promise<CourseInfo[]> {
	const url = `${MAIN_ENDPOINT}/${COURSE_ENDPOINT.GET_COURSES}`;
	const { data } = await axios.get<CourseInfo[]>(url);

	return data;
}
