import React, { ReactElement } from 'react';
import { Link } from 'react-router-dom';
import { Paths } from '../../../router/routes';
import Checkbox from '../Form/Checkbox/Checkbox';
import HorizontalLine from '../HorizontalLine/HorizontalLine';
import Logo from '../Logo/Logo';

import './Register.scss';

export default function Register(): ReactElement {
	return (
		<div id="register-component">
			<Logo withWordmark height="72px" wordMarkHeight="56px" />
			{renderFormContent()}
		</div>
	);
}

function renderFormContent(): ReactElement {
	return (
		<>
			<h1 id="register-component__title" className="text--18 text--semi-bold">
				ลงทะเบียนเข้าใช้ CourseLagoon
			</h1>

			<HorizontalLine width="144px" />

			<div className="margin--top-16" style={{ width: '100%' }}>
				<div id="register-component__names">
					<input className="no-icon" type="text" placeholder="ชื่อ" />
					<input className="no-icon" type="text" placeholder="นามสกุล" />
				</div>

				<input className="icon icon--email" type="email" placeholder="อีเมล" />
				<input className="icon icon--unlocked" type="password" placeholder="รหัสผ่าน" />
				<input
					className="icon icon--checked"
					type="password"
					placeholder="ยืนยันรหัสผ่าน"
				/>

				<Checkbox text={renderCheckboxText()} id="register-component__tos-check" />

				<button type="button">ลงทะเบียน</button>

				<div id="register-component__login" className="margin--top-16 text--14">
					มีบัญชีผู้ใช้แล้ว?&nbsp;
					<Link to={Paths.Login} className="link">
						คลิกเพื่อเข้าสู่ระบบ
					</Link>
				</div>
			</div>
		</>
	);
}

function renderCheckboxText(): ReactElement {
	const termsLink = (
		<a href="https://example.com" className="link">
			ข้อกำหนดและเงื่อนไขในการใช้งาน
		</a>
	);

	const privacyPolicyLink = (
		<a href="https://example.com" className="link">
			นโยบายความเป็นส่วนตัว
		</a>
	);

	return (
		<>
			ฉันยอมรับใน {termsLink} และ {privacyPolicyLink} ของ CourseLagoon
		</>
	);
}
