import React, { ReactElement, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { Paths } from '../../../router/routes';
import history, { navigate } from '../../../services/historyService';
import { RootState } from '../../../store';
import { wordpressLogin } from '../../../store/authSlice';
import HorizontalLine from '../HorizontalLine/HorizontalLine';
import Logo from '../Logo/Logo';

import './Login.scss';

export default function Login(): ReactElement {
	// const token = useSelector((state: RootState) => state.auth)?.userInfo?.token;
	const [username, setUsername] = useState('');
	const [password, setPassword] = useState('');

	const dispatch = useDispatch();

	return (
		<div id="login-component">
			<Logo withWordmark height="72px" wordMarkHeight="56px" />
			<h1 id="login-component__title" className="text--18 text--semi-bold">
				เข้าสู่ระบบ CourseLagoon
			</h1>

			<HorizontalLine width="144px" />

			<div className="margin--top-16" style={{ width: '100%' }}>
				<input
					className="icon icon--email"
					type="text"
					placeholder="อีเมล"
					value={username}
					onChange={e => setUsername((e.target as HTMLInputElement).value)}
				/>
				<input
					className="icon icon--unlocked"
					type="password"
					placeholder="รหัสผ่าน"
					value={password}
					onChange={e => setPassword((e.target as HTMLInputElement).value)}
				/>

				<a href="https://example.com" className="link" id="login-component__forgotten-password">
					ลืมรหัสผ่าน?
				</a>

				<button type="button" onClick={login}>
					เข้าสู่ระบบ
				</button>

				<div id="login-component__sign-up" className="margin--top-16 text--14">
					ยังไม่ได้เป็นสมาชิก? &nbsp;
					<Link to={Paths.Register} className="link">
						คลิกเพื่อสมัคร
					</Link>
				</div>
			</div>
		</div>
	);

	function login(): void {
		dispatch(wordpressLogin(username, password));
	}
}
