/* eslint-disable react/no-danger */
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { matchPath } from 'react-router-dom';
import CourseCard from '../../components/common/CourseCard/CourseCard';
import { Paths } from '../../router/routes';
import { CourseInfo, getCourseById, getCourses } from '../../services/courseService';
import { navigate } from '../../services/historyService';
import { getLessonsFromCourse } from '../../services/lessonService';
import { getUsers } from '../../services/userService';
import { RootState } from '../../store';

// eslint-disable-next-line no-restricted-globals
const path = matchPath(location.pathname, {
	path: Paths.Course,
	exact: true,
});

const courseId = (path?.params as any)?.courseId;

function Course(): React.ReactElement {
	const token = useSelector((state: RootState) => state.auth)?.userInfo?.token;
	
	const [course, setCourse] = useState<CourseInfo>();
	const [lessons, setLessons] = useState<any[]>([]);
	const [users, setUsers] = useState<any[]>([]);

	useEffect(() => {
		if (!courseId || !token) return;

		getCourseById(courseId)
			.then(c => {
				setCourse(c?.[0]);
				return c;
			})
			.catch(() => {});

		getUsers()
			.then(u => {
				setUsers(u);
				return u;
			})
			.catch(() => {});

		getLessonsFromCourse(courseId, token)
			.then(l => {
				setLessons(l);
				return l;
			})
			.catch(() => {});
	}, [token]);

	return (
		<div style={{ padding: '64px' }}>
			<div
				dangerouslySetInnerHTML={{
					__html: course?.content?.rendered ?? '',
				}}
			/>
		</div>
	);
}

export default Course;
