import React, { CSSProperties, ReactElement } from 'react';

import './Checkbox.scss';

interface CheckboxProps {
	id: string;
	name?: string;
	text?: string | ReactElement;
	checkboxStyle?: CSSProperties;
	inputStyle?: CSSProperties;
	labelStyle?: CSSProperties;
}

export default function Checkbox(props: CheckboxProps): ReactElement<CheckboxProps> {
	const { id, name, text, checkboxStyle, inputStyle, labelStyle } = props;

	return (
		<div style={checkboxStyle} id={id}>
			<input
				className="cl-checkbox"
				id={`${id}__input`}
				name={name}
				style={inputStyle}
				type="checkbox"
			/>
			<label style={labelStyle} htmlFor={`${id}__input`} className="cl-checkbox-label">
				{text}
			</label>
		</div>
	);
}
